import React from 'react'
import Image from 'next/image'
import Link from 'next/link'
import NavLinks from './nav-links'
import { Button } from '../button'

export default function NavBar() {
    return (
        <nav>
            <nav className='fixed flex flex-row h-20 top-0 z-50 w-full justify-between bg-[#FFFFFF] border-b-[1px solid #00000033] '>
                <div className='flex flex-row h-14 px-28 items-center py-9'>
                    <Link
                        className=" items-start justify-center rounded-md bg-[#FFFFFF] p-4 md:h-20"
                        href="/"
                    >
                        <div className="w-32 text-white md:w-40">
                            <Image
                                src="/logo.png"
                                width={115}
                                height={37}
                                className="block"
                                alt="Logo"
                            />
                        </div>
                    </Link>
                    <div className="flex flex-row items-center justify-center   bg-white p-4">
                        <NavLinks />
                    </div>
                    <div className="items-end justify-center   bg-white p-4">
                        <Button>Book A Call</Button>
                    </div>
                </div>

            </nav>
        </nav>
    )
}
